'use client';

import { useState, useEffect } from 'react';
import { products } from '@/data/products';

interface ShareProductProps {
  productId: string;
  productName: string;
  productImage?: string;
  className?: string;
  size?: 'sm' | 'md' | 'lg';
  isOpen?: boolean;
  onClose?: () => void;
}

export default function ShareProduct({ 
  productId, 
  productName, 
  productImage,
  className = '',
  size = 'md',
  isOpen: externalIsOpen,
  onClose: externalOnClose
}: ShareProductProps) {
  const [isOpen, setIsOpen] = useState(externalIsOpen || false);
  const [copied, setCopied] = useState(false);

  // Update local state when external isOpen changes
  useEffect(() => {
    if (externalIsOpen !== undefined) {
      setIsOpen(externalIsOpen);
    }
  }, [externalIsOpen]);

  const handleClose = () => {
    setIsOpen(false);
    if (externalOnClose) {
      externalOnClose();
    }
  };

  const siteUrl = process.env.NEXT_PUBLIC_SITE_URL || 'https://raagafoods.com';
  const productUrl = `${siteUrl}/products/${productId}`;
  const shareText = `Check out this amazing ${productName} from Raaga Foods - Premium authentic South Indian food products!`;
  const shareImage = productImage || '/products/placeholder.jpg';

  const sizes = {
    sm: 'w-4 h-4',
    md: 'w-5 h-5',
    lg: 'w-6 h-6'
  };

  const buttonSizes = {
    sm: 'p-1.5 text-xs',
    md: 'p-2 text-sm',
    lg: 'p-3 text-base'
  };

  const shareOptions = [
    {
      name: 'Facebook',
      icon: 'ðŸ“˜',
      color: 'bg-blue-600 hover:bg-blue-700',
      url: `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(productUrl)}&quote=${encodeURIComponent(shareText)}`
    },
    {
      name: 'Twitter',
      icon: 'ðŸ¦',
      color: 'bg-sky-500 hover:bg-sky-600',
      url: `https://twitter.com/intent/tweet?text=${encodeURIComponent(shareText)}&url=${encodeURIComponent(productUrl)}&hashtags=RaagaFoods,SouthIndianFood,AuthenticFood`
    },
    {
      name: 'WhatsApp',
      icon: 'ðŸ’¬',
      color: 'bg-green-600 hover:bg-green-700',
      url: `https://wa.me/?text=${encodeURIComponent(`${shareText} ${productUrl}`)}`
    },
    {
      name: 'LinkedIn',
      icon: 'ðŸ’¼',
      color: 'bg-blue-700 hover:bg-blue-800',
      url: `https://www.linkedin.com/sharing/share-offsite/?url=${encodeURIComponent(productUrl)}`
    },
    {
      name: 'Pinterest',
      icon: 'ðŸ“Œ',
      color: 'bg-red-600 hover:bg-red-700',
      url: `https://pinterest.com/pin/create/button/?url=${encodeURIComponent(productUrl)}&media=${encodeURIComponent(`${siteUrl}${shareImage}`)}&description=${encodeURIComponent(shareText)}`
    },
    {
      name: 'Email',
      icon: 'ðŸ“§',
      color: 'bg-gray-600 hover:bg-gray-700',
      url: `mailto:?subject=${encodeURIComponent(`Check out ${productName} from Raaga Foods`)}&body=${encodeURIComponent(`${shareText}\n\n${productUrl}`)}`
    }
  ];

  const handleShare = (url: string, platform: string) => {
    // Track share event (you can add analytics here)
    console.log(`Shared ${productName} on ${platform}`);
    
    // Open share URL
    window.open(url, '_blank', 'noopener,noreferrer,width=600,height=400');
    handleClose();
  };

  const handleCopyLink = async () => {
    try {
      await navigator.clipboard.writeText(productUrl);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (error) {
      // Fallback for browsers that don't support clipboard API
      const textArea = document.createElement('textarea');
      textArea.value = productUrl;
      document.body.appendChild(textArea);
      textArea.select();
      document.execCommand('copy');
      document.body.removeChild(textArea);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    }
  };

  const handleNativeShare = async () => {
    if (navigator.share) {
      try {
        await navigator.share({
          title: productName,
          text: shareText,
          url: productUrl
        });
      } catch (error) {
        console.log('Share cancelled or failed');
      }
    }
  };

  return (
    <div className={`relative ${className}`}>
      {/* Share Button */}
      <button
        onClick={() => setIsOpen(!isOpen)}
        className={`inline-flex items-center space-x-2 ${buttonSizes[size]} bg-orange-100 text-orange-600 hover:bg-orange-200 rounded-lg transition-colors duration-200 border border-orange-200 hover:border-orange-300`}
        title="Share this product"
      >
        <svg className={sizes[size]} fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8.684 13.342C8.886 12.938 9 12.482 9 12c0-.482-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l6.632 3.316m-6.632-6l6.632-3.316m0 0a3 3 0 105.367-2.684 3 3 0 00-5.367 2.684zm0 9.316a3 3 0 105.367 2.684 3 3 0 00-5.367-2.684z" />
        </svg>
        <span>Share</span>
      </button>

      {/* Share Dropdown */}
      {isOpen && (
        <>
          {/* Backdrop */}
          <div 
            className="fixed inset-0 z-40" 
            onClick={handleClose}
          />
          
          {/* Share Options */}
          <div className="absolute top-full mt-2 left-0 z-50 bg-white rounded-lg shadow-xl border border-gray-200 min-w-80 p-4">
            <div className="flex justify-between items-center mb-4">
              <h3 className="font-semibold text-gray-900">Share this product</h3>
              <button
                onClick={handleClose}
                className="text-gray-400 hover:text-gray-600"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            {/* Product Preview */}
            <div className="flex items-center space-x-3 mb-4 p-3 bg-gray-50 rounded-lg">
              <div className="w-12 h-12 bg-gray-200 rounded-lg flex items-center justify-center">
                {shareImage ? (
                  <img
                    src={shareImage}
                    alt={productName}
                    className="w-full h-full object-cover rounded-lg"
                  />
                ) : (
                  <svg className="w-6 h-6 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
                  </svg>
                )}
              </div>
              <div className="flex-1 min-w-0">
                <p className="font-medium text-gray-900 truncate">{productName}</p>
                <p className="text-sm text-gray-500">Raaga Foods</p>
              </div>
            </div>

            {/* Native Share (Mobile) */}
            {navigator.share && (
              <button
                onClick={handleNativeShare}
                className="w-full mb-3 p-3 bg-orange-600 text-white rounded-lg hover:bg-orange-700 transition-colors duration-200 flex items-center justify-center space-x-2"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8.684 13.342C8.886 12.938 9 12.482 9 12c0-.482-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l6.632 3.316m-6.632-6l6.632-3.316m0 0a3 3 0 105.367-2.684 3 3 0 00-5.367 2.684zm0 9.316a3 3 0 105.367 2.684 3 3 0 00-5.367-2.684z" />
                </svg>
                <span>Share via device</span>
              </button>
            )}

            {/* Social Share Options */}
            <div className="grid grid-cols-3 gap-2 mb-4">
              {shareOptions.map((option) => (
                <button
                  key={option.name}
                  onClick={() => handleShare(option.url, option.name)}
                  className={`${option.color} text-white p-3 rounded-lg transition-colors duration-200 flex flex-col items-center space-y-1`}
                  title={`Share on ${option.name}`}
                >
                  <span className="text-lg">{option.icon}</span>
                  <span className="text-xs font-medium">{option.name}</span>
                </button>
              ))}
            </div>

            {/* Copy Link */}
            <div className="border-t border-gray-200 pt-4">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Product Link
              </label>
              <div className="flex items-center space-x-2">
                <input
                  type="text"
                  value={productUrl}
                  readOnly
                  className="flex-1 px-3 py-2 border border-gray-300 rounded-lg bg-gray-50 text-sm"
                />
                <button
                  onClick={handleCopyLink}
                  className={`px-4 py-2 rounded-lg text-sm font-medium transition-all duration-200 ${
                    copied
                      ? 'bg-green-100 text-green-800 border border-green-200'
                      : 'bg-gray-100 text-gray-700 border border-gray-200 hover:bg-gray-200'
                  }`}
                >
                  {copied ? (
                    <div className="flex items-center space-x-1">
                      <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                      </svg>
                      <span>Copied!</span>
                    </div>
                  ) : (
                    <div className="flex items-center space-x-1">
                      <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
                      </svg>
                      <span>Copy</span>
                    </div>
                  )}
                </button>
              </div>
            </div>

            {/* Share Stats (Optional) */}
            <div className="mt-4 text-center">
              <p className="text-xs text-gray-500">
                Help others discover authentic South Indian flavors!
              </p>
            </div>
          </div>
        </>
      )}
    </div>
  );
}

// Compact Share Button for Product Cards
export function ShareButton({ 
  productId, 
  productName, 
  className = '' 
}: { 
  productId: string; 
  productName: string; 
  className?: string; 
}) {
  const [isSharing, setIsSharing] = useState(false);

  const handleQuickShare = async () => {
    const productUrl = `${process.env.NEXT_PUBLIC_SITE_URL || 'https://raagafoods.com'}/products/${productId}`;
    const shareText = `Check out ${productName} from Raaga Foods!`;

    setIsSharing(true);

    if (navigator.share) {
      try {
        await navigator.share({
          title: productName,
          text: shareText,
          url: productUrl
        });
      } catch (error) {
        // User cancelled or error occurred
      }
    } else {
      // Fallback to WhatsApp share
      const whatsappUrl = `https://wa.me/?text=${encodeURIComponent(`${shareText} ${productUrl}`)}`;
      window.open(whatsappUrl, '_blank', 'noopener,noreferrer');
    }

    setTimeout(() => setIsSharing(false), 1000);
  };

  return (
    <button
      onClick={handleQuickShare}
      disabled={isSharing}
      className={`p-2 text-gray-400 hover:text-orange-600 hover:bg-orange-50 rounded-full transition-all duration-200 ${className}`}
      title={`Share ${productName}`}
    >
      {isSharing ? (
        <svg className="w-4 h-4 animate-spin" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
        </svg>
      ) : (
        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8.684 13.342C8.886 12.938 9 12.482 9 12c0-.482-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l6.632 3.316m-6.632-6l6.632-3.316m0 0a3 3 0 105.367-2.684 3 3 0 00-5.367 2.684zm0 9.316a3 3 0 105.367 2.684 3 3 0 00-5.367-2.684z" />
        </svg>
      )}
    </button>
  );
}
