import React from 'react';
import StarRating from './StarRating';
import { getAverageRating, getReviewCount, reviewsData } from '@/data/reviews';

const ReviewsSummary: React.FC = () => {
  const totalReviews = reviewsData.length;
  const avgOverallRating = totalReviews > 0 
    ? reviewsData.reduce((sum, review) => sum + review.rating, 0) / totalReviews 
    : 0;
  
  const verifiedReviews = reviewsData.filter(review => review.verified).length;
  const recentReviews = reviewsData
    .sort((a, b) => new Date(b.dateCreated).getTime() - new Date(a.dateCreated).getTime())
    .slice(0, 3);

  const topRatedProducts = [
    { id: 'dosa-batter', name: 'Dosa Batter' },
    { id: 'mango-pickle', name: 'Mango Pickle' },
    { id: 'sambar-powder', name: 'Sambar Powder' }
  ].map(product => ({
    ...product,
    rating: getAverageRating(product.id),
    reviewCount: getReviewCount(product.id)
  })).filter(product => product.reviewCount > 0)
    .sort((a, b) => b.rating - a.rating);

  if (totalReviews === 0) return null;

  return (
    <div className="bg-gradient-to-r from-orange-50 to-amber-50 rounded-xl border border-orange-200 p-8 mb-12">
      <div className="text-center mb-8">
        <h2 className="text-3xl font-bold text-gray-900 mb-2">
          What Our Customers Say
        </h2>
        <p className="text-gray-600">
          Real reviews from verified customers who love our authentic flavors
        </p>
      </div>

      <div className="grid md:grid-cols-3 gap-8 mb-8">
        {/* Overall Stats */}
        <div className="text-center bg-white rounded-lg p-6 shadow-sm">
          <div className="text-4xl font-bold text-orange-600 mb-2">
            {avgOverallRating.toFixed(1)}
          </div>
          <StarRating rating={avgOverallRating} size="lg" className="justify-center mb-2" />
          <div className="text-gray-600">
            <div className="font-semibold">{totalReviews} Total Reviews</div>
            <div className="text-sm">{verifiedReviews} Verified Purchases</div>
          </div>
        </div>

        {/* Top Products */}
        <div className="bg-white rounded-lg p-6 shadow-sm">
          <h3 className="font-bold text-gray-900 mb-4 text-center">Top Rated Products</h3>
          <div className="space-y-3">
            {topRatedProducts.map((product, index) => (
              <div key={product.id} className="flex items-center justify-between">
                <div className="flex items-center gap-2">
                  <span className="text-orange-600 font-bold">#{index + 1}</span>
                  <span className="text-sm font-medium text-gray-800">{product.name}</span>
                </div>
                <div className="flex items-center gap-1">
                  <StarRating rating={product.rating} size="sm" />
                  <span className="text-xs text-gray-500">({product.reviewCount})</span>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Recent Reviews */}
        <div className="bg-white rounded-lg p-6 shadow-sm">
          <h3 className="font-bold text-gray-900 mb-4 text-center">Recent Reviews</h3>
          <div className="space-y-3">
            {recentReviews.map((review) => (
              <div key={review.id} className="border-b border-gray-100 pb-2 last:border-b-0">
                <div className="flex items-center gap-2 mb-1">
                  <StarRating rating={review.rating} size="sm" />
                  {review.verified && (
                    <span className="text-xs text-green-600">âœ“</span>
                  )}
                </div>
                <p className="text-xs text-gray-600 line-clamp-2">
                  "{review.comment.substring(0, 80)}..."
                </p>
                <div className="text-xs text-gray-400 mt-1">
                  - {review.userName}
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Customer Satisfaction */}
      <div className="bg-white rounded-lg p-6 shadow-sm">
        <div className="grid md:grid-cols-4 gap-6 text-center">
          <div>
            <div className="text-2xl font-bold text-green-600 mb-1">
              {Math.round((verifiedReviews / totalReviews) * 100)}%
            </div>
            <div className="text-sm text-gray-600">Verified Purchases</div>
          </div>
          <div>
            <div className="text-2xl font-bold text-blue-600 mb-1">
              {Math.round((reviewsData.filter(r => r.rating >= 4).length / totalReviews) * 100)}%
            </div>
            <div className="text-sm text-gray-600">4+ Star Reviews</div>
          </div>
          <div>
            <div className="text-2xl font-bold text-purple-600 mb-1">
              {reviewsData.filter(r => r.topReviewer).length}
            </div>
            <div className="text-sm text-gray-600">Top Reviewers</div>
          </div>
          <div>
            <div className="text-2xl font-bold text-orange-600 mb-1">
              {reviewsData.filter(r => r.images && r.images.length > 0).length}
            </div>
            <div className="text-sm text-gray-600">Photo Reviews</div>
          </div>
        </div>
      </div>

      {/* Call to Action */}
      <div className="text-center mt-6">
        <p className="text-gray-600 mb-3">
          Join thousands of happy customers who trust Raaga Foods
        </p>
        <button
          onClick={() => {
            const productsSection = document.getElementById('all-products');
            if (productsSection) {
              const navbar = document.querySelector('nav');
              const navbarHeight = navbar ? navbar.getBoundingClientRect().height : 0;
              const elementPosition = productsSection.getBoundingClientRect().top + window.pageYOffset;
              const offsetPosition = elementPosition - navbarHeight - 20;
              
              window.scrollTo({
                top: offsetPosition,
                behavior: 'smooth'
              });
            }
          }}
          className="bg-orange-500 text-white px-8 py-3 rounded-lg hover:bg-orange-600 transition-colors font-medium inline-flex items-center gap-2"
        >
          Explore Our Products
          <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
          </svg>
        </button>
      </div>
    </div>
  );
};

export default ReviewsSummary;
